html,
body {
	margin: 0;
	padding: 0;
}
a {
	text-decoration: none;
}

img {
	display: block;
}

.img-responsive {
	max-width: 100%;
	height: auto;
	display: block;
}

.site-footer .footer-logo-resize {
	max-width: 75%;
	min-width: 100px;
	width: max(100px, 75%);
	height: auto;
	display: block;
}
